.post {
  .head-post{
    box-shadow: 0 4px 12px 0 rgba(0,0,0,.05);
    @include mGrid{
      box-shadow: none;
    }
  }
  .col-centered{
    float: none;
    margin: 0 auto;
    border: solid 1px #000;
  }
  .content-post{
    padding: 15px;
    width: 100%; 
    overflow-x: hidden; /* show only horizontal scrollbar */
    .title-post {
      font-size: 3rem;
    }
    p{
      font-size: 1.1em;
    }
    .breadcrumb {
      list-style: none;
      margin-left: 0;
      li {
        display: inline;
        font-size: 0.8em;
        a {
          color: #888;
          text-decoration: none;
        }
        &:last-child{
          font-weight: bold;
        }
      }
      li+li {
        &:before{
          padding: 8px;
          color: #888;
          content: "/\00a0";
        }
        
      }
    }
    .publish-date{
      font-size: 0.8em;
      color: #888;
      margin-left: 2px;
      margin-top: -10px;
      margin-bottom: 30px;
    }
   
  }
}